import i18next from "./lang/i18n";

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

const COLORS = {
    customBlue3: '#556273',
    customBlue2: '#69788c',
    customBlue1: '#8a99a6',
    customGray: '#d9d9d9',
    customWhite: '#fafafa',
};

const FONT_SIZE = {
    titleText3: isPortrait ? '4rem' : '7rem',
    titleText2: isPortrait ? '1.8rem' : '3.5rem',
    titleText1: isPortrait ? '1.6rem' : '2.5rem',
    bodyText2: isPortrait ? '1.3rem' : '2.2rem',
    bodyText1: isPortrait ? '1.1rem' : '1.9rem',
    smallText: isPortrait ? '1.0rem' : '1.6rem',
    smallText2: isPortrait ? '0.9rem' : '1.2rem',
    smallText3: isPortrait ? '0.5rem' : '0.9rem'
}

enum FONT_NAME {
    PALATINO_LINOTYPE= 'Palatino Linotype',
    EB_GARAMOND= 'EB Garamond',
    MERRIWEATHER= 'Merriweather',
    LATO= 'Lato',
    ALEX_BRUSH= 'Alex Brush',
    ARIAL= 'Arial',
}

type FONT_NAME_TYPE = keyof typeof FONT_NAME;

const getI18nFont = (name: FONT_NAME): string => {
    if (i18next.resolvedLanguage === 'ko') return 'Noto Serif KR';
    return name;
}

export { COLORS, FONT_SIZE, FONT_NAME, getI18nFont };